html{
  scroll-behavior: smooth;
}*::selection{
  background-color: var(--color-gray-dark);
  color: var(--color-white);
}.hr{
  width: 100%;
  height: 2px;
  background-color: black;
}body{
  --color-gray: rgb(165, 165, 145);
  --color-gray-dark: rgb(139, 133, 117);
  --color-white:rgb(248, 245, 239);
  margin: 0;
  padding: 0;
  background-color: var(--color-white);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}.navbar-section{
  position: sticky;
  z-index: 9999;
  top: 0;
  width: 100%;
  background-color: var(--color-gray);
  border-bottom: 2px solid black;
}.navbar-section .navbar{
  width: 87%;
  margin: 0 auto;
  display: flex;
}.navbar-section .navbar li{
  cursor: pointer;
  transition: 150ms;
  display: flex;
  align-items: center;
  list-style: none;
  padding: 10px 0px;
}.navbar-section .navbar li:hover a{
  scale: 1.1;
}.navbar-section .navbar li a{
  transition: 150ms;
  padding: 0px 15px;
  font-weight: 600;
  color: black;
  text-decoration: none;
}.navbar-section .navbar li:first-child{
  transform: translateY(5%);
}













.first-section{
  margin: 100px auto;
  display: flex;
  width: 80%;
}.first-section .first-section-left{
  width: 50%;
}.first-section .first-section-left h1{
  font-size: 75px;
  font-weight: 800;
  margin: 0;
  padding: 0;
}.first-section .first-section-left p{
  color: var(--color-gray-dark);
  font-weight: 700;
  margin: 0;
  padding: 0;
  padding-top: 15px;
}.first-section .first-section-left span{
  color: black;
}.first-section .first-section-left button{
  cursor: pointer;
  transition: 150ms;
  border: 2px solid black;
  box-shadow: 4px 4px 0px black;
  margin-top: 20px;
  background-color: var(--color-gray);
  padding: 8px 16px;
}.first-section .first-section-left button:hover{
  box-shadow: 2px 2px 0px black;
  transform: translateX(2px);
  background-color: var(--color-gray-dark);
  transform: translateY(2px);
}.first-section .first-section-left button a{
  font-weight: 700;
  color: black;
  text-decoration: none;
}.first-section-right{
  width: 45%;
  height: 350px;

}.first-section-right img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}




.first-section-span{
  transition: 200ms;
  animation: typing 2s step-end infinite alternate;
}

@keyframes typing{
  0%{
    transform: translateY(0);
    opacity: 1;
  }100%{
    transform: translateY(100%);
    opacity: 0;
  }
}










.navigation-section{
  width: 100%;
  height: 2px;
  background-color: black;
}.navigation-section .navigation-panel{
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  transform: translateY(-50%);
}.navigation-section .navigation-panel li{
  cursor: pointer;
  transition: 150ms;
  display: flex;
  align-items: center;
  font-weight: 700;
  width: calc(100% / 3);
  list-style: none;
  padding: 15px 20px;
  background-color: var(--color-gray-dark);
  border: 2px solid black;
  border-right: none;
}.navigation-section .navigation-panel li:hover{
  box-shadow: 4px 4px 0px black;
  transform: translate(-4px, -4px);
  border-right: 2px solid black;
}.navigation-section .navigation-panel li:last-child{
  border-right: 2px solid black;
}.mrg-left{
  margin-left: auto;
}.navigation-section .navigation-panel li a{
  color: black;
  text-decoration: none;
}



















.projects-section{
  border-bottom: 2px solid black;
  box-sizing: border-box;
  margin: 0;
  padding: 50px;
  width: 100%;
  background-color: var(--color-gray);
}
.projects-section .projects-section-content{
  width: 90%;
  margin: 25px auto;
}.projects-section .projects-section-content h1{
  font-size: 45px;
  padding: 0;
  margin: 0;
}.projects-cards{
  display: flex;
  padding: 5px;
  gap: 20px;
  overflow-y: none;
  overflow-x: auto;
}.projects-cards::-webkit-scrollbar{
  background-color: none;
  border: none;
} .projects-cards::-webkit-scrollbar-corner{
  background-color: var(--color-gray-dark);
  border-radius: 32px;
  padding: 4px 8px;
} .projects-cards .projecst-card{
  width: calc(100% / 2.5);
  transition: 200ms;
  min-width: calc(100% / 2.5);
  height: 500px;
  margin-top: 10px;
  box-shadow: 6px 6px 0px black;
  border: 2px solid black;
  background-color: var(--color-white);
}.projects-cards .projecst-card:hover{
  box-shadow: 2px 2px 0px black;
  transform: translate(2px, 2px);
}.projects-cards .projecst-card p{
  background-color: var(--color-gray-dark);
  margin: 0;
  padding: 5px;
  font-weight: 700;
  border-bottom: 2px solid black;
}.projects-cards .projecst-card .projects-card-img{
  width: 100%;
  height: 100px;
  border-bottom: 2px solid black;
}.projects-cards .projecst-card .projects-card-text{
  padding: 15px 30px;
}.projects-cards .projecst-card .projects-card-text h3{
  margin: 0;
  font-size: 30px;
  padding: 0;
}.projects-cards .projecst-card button{
  cursor: pointer;
  transition: 150ms;
  border: 2px solid black;
  box-shadow: 2px 2px 0px black;
  margin-top: 20px;
  background-color: var(--color-gray);
  padding: 8px 16px;
}.projects-cards .projecst-card button a{
  font-weight: 700;
  color: black;
  text-decoration: none;
}.projects-cards .projecst-card button:hover{
  box-shadow: 1px 1px 0px black;
  transform: translateX(1px);
  background-color: var(--color-gray-dark);
  transform: translateY(1px);
}.projects-cards .projecst-card .projects-card-text .project-tools-list{
  display: flex;
  margin: 0;
  padding: 0;
  gap: 5px;
}.projects-cards .projecst-card .projects-card-text .project-tools-list li{
  list-style: none;
  transition: 150ms;
  color: var(--color-gray);
}.projects-cards .projecst-card .projects-card-text .project-tools-list li:hover{
  color: var(--color-gray-dark);
}





















.about-section{
  display: flex;
  gap: 30px;
  width: 80%;
  padding-bottom: 50px;
  margin: 100px auto;
}.about-section .about-section-photo{
  width: 45%;
  height: 525px;
  background-color: var(--color-gray-dark);
  box-shadow: 8px 8px 0px black;
  border: 2px solid black;
}.about-section .about-section-infos{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}.about-section .about-section-infos h1{
  font-size: 45px;
  margin: 0;
  padding: 0;
}.about-section .about-section-infos p{
  font-size: 14px;
  font-weight: 600;
}.about-section .about-section-infos h4{
  margin: 0;
  margin-top: 10px;
  font-family: 700;
  padding: 0;
  font-size: 15px;
}.about-section .about-section-infos a{
  font-size: 15px;
  font-weight: 600;
  color: var(--color-gray-dark);
}





















.skills-section{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 50px 0px;
  background-color: var(--color-gray);
  border-bottom: 2px solid black;
}.skills-section .skills-section-content{
  width: 80%;
}.skills-section .skills-section-content h1{
  margin: 0;
  font-size: 45px;
  padding: 0;
}.skills-section .skills-section-content .skills-card{
  width: 100%;
  border: 2px solid black;
  background-color: var(--color-white);
  height: 500px;
  box-shadow: 6px 6px 0px black;
}.skills-section .skills-section-content .skills-content-down{
  width: 100%;
  height: calc(100% );
  display: flex;
  flex-wrap: nowrap;
}.skills-section .skills-section-content .skills-card h4{
  margin: 0;
  padding: 5px;
  background-color: var(--color-gray-dark);
  border-bottom: 2px solid black;
  font-weight: 700;
}.skills-card .skills-section-lines{
  width: 50%;
  box-sizing: border-box;
  padding: 10px 20px;
  height: calc(100% - 33px);
}.skills-card .skills-section-lines .skills-text-line{
  display: flex;
  align-items: center;
}.skills-card .skills-section-lines .skills-text-line h3{
  font-weight: 700;
  font-size: 18px;
}.skills-line{
  padding-bottom: 10px;
  position: relative;
  border: 2px solid black;
  width: 100%;
  height: 10px;
  background-color: var(--color-gray);
}.skills-line-1{
  position: absolute;
  height: 100%;
  border-right: 2px solid black;
  width: 70%;
  background-color: var(--color-gray-dark);
}.skills-line-2{
  position: absolute;
  height: 100%;
  border-right: 2px solid black;
  width: 82%;
  background-color: var(--color-gray-dark);
}.skills-line-3{
  position: absolute;
  height: 100%;
  border-right: 2px solid black;
  width: 34%;
  background-color: var(--color-gray-dark);
}.skills-line-4{
  position: absolute;
  height: 100%;
  border-right: 2px solid black;
  width: 13%;
  background-color: var(--color-gray-dark);
}.skills-line-5{
  position: absolute;
  height: 100%;
  border-right: 2px solid black;
  width: 61%;
  background-color: var(--color-gray-dark);
}.skills-text-section{
  height: calc(100% - 33px);
  width: calc(50% - 2px);
  border-left: 2px solid black;
  box-sizing: border-box;
  padding: 10px 20px;
  height: calc(100% - 33px);
}.skills-text-section p{
  font-weight: 600;
  font-size: 14px;
}

















.footer-section{
  width: 100%;
  background-color: black;
  border-bottom: 2px solid black;
}.footer-section .footer{
  display: flex;
  justify-content: end;
  width: 87%;
  margin: 0 auto;
}.footer-section .footer li{
  cursor: pointer;
  transition: 150ms;
  display: flex;
  align-items: center;
  list-style: none;
  padding: 10px 0px;
}.footer-section .footer li:first-child{
  margin-right: auto;
}.footer-section .footer li:hover a{
  scale: 1.1;
}.footer-section .footer li a{
  transition: 150ms;
  padding: 0px 15px;
  font-weight: 600;
  color: var(--color-white);
  text-decoration: none;
}.footer-section .navbar li:first-child{
  transform: translateY(5%);
}





